import {
  TextField,
  FormControl,
  InputLabel,
  Select,
  OutlinedInput,
  MenuItem,
  Checkbox,
  ListItemText,
  FormControlLabel,
  Button,
  SelectChangeEvent,
} from "@mui/material"
import {
  LocalizationProvider,
  DatePicker,
  TimePicker,
} from "@mui/x-date-pickers"
import { AdapterDayjs } from "@mui/x-date-pickers/AdapterDayjs"
import dayjs, { Dayjs } from "dayjs"
import React, { useCallback, useEffect } from "react"
import { useState } from "react"
import { useRenderData } from "streamlit-component-lib-react-hooks"
import { names } from "./consts"
import { Streamlit } from "streamlit-component-lib"

interface Data {
  date: Dayjs
  fromTime: Dayjs
  interval: number
  showType: typeof names
  filterLength: FilterLength
}

interface FilterLength {
  filter: boolean
  minLength: number
  maxLength: number
}

interface ReturnData {
  date: string
  fromTime: string
  interval: number
  showType: typeof names
  filterLength: FilterLength
}

export const SelectionHeader: React.FC = () => {
  const renderData = useRenderData()
  const initialDate = dayjs(renderData.args["date"])
  const initialData: Data = {
    date: initialDate,
    fromTime: initialDate,
    interval: 0,
    showType: names,
    filterLength: {
      filter: false,
      minLength: 0,
      maxLength: 0,
    },
  }
  const [data, setData] = useState<Data>(initialData)

  useEffect(() => {
    const returnData: ReturnData = {
      ...data,
      date: data.date.format("YYYY-MM-DD HH:mm:ss"),
      fromTime: data.fromTime.format("YYYY-MM-DD HH:mm:ss"),
    }
    Streamlit.setComponentValue(returnData)
  }, [data])

  const handleChange = (event: SelectChangeEvent<typeof names>) => {
    const {
      target: { value },
    } = event
    setData((data) => {
      data.showType = typeof value === "string" ? value.split(",") : value
      return { ...data }
    })
  }

  const handleBackwardClick = () => {
    setData((data) => {
      data.fromTime = data.fromTime.subtract(data.interval, "minutes")
      return { ...data }
    })
  }

  const handleForwardClick = () => {
    setData((data) => {
      data.fromTime = data.fromTime.add(data.interval, "minutes")
      return { ...data }
    })
  }

  const ITEM_HEIGHT = 48
  const ITEM_PADDING_TOP = 8
  const MenuProps = {
    PaperProps: {
      style: {
        maxHeight: ITEM_HEIGHT * 4.5 + ITEM_PADDING_TOP,
        width: 250,
      },
    },
  }

  return (
    <div style={{ marginTop: 30 }}>
      <LocalizationProvider dateAdapter={AdapterDayjs}>
        <DatePicker
          label="Date"
          format="YYYY-MM-DD"
          value={data.date}
          onChange={(newValue) => {
            if (newValue)
              setData((data) => {
                data.date = newValue
                return { ...data }
              })
          }}
          sx={{
            backgroundColor: "background.default",
            borderRadius: 1,
            marginRight: 2,
          }}
        />
        <TimePicker
          label="From time"
          value={data.fromTime}
          onChange={(newValue) => {
            if (newValue) {
              setData((data) => {
                data.fromTime = newValue
                return { ...data }
              })
            }
          }}
          ampm={false}
          sx={{
            backgroundColor: "background.default",
            borderRadius: 1,
            marginRight: 2,
          }}
        />
      </LocalizationProvider>
      <TextField
        label="Interval in minutes"
        id="outlined-start-adornment"
        sx={{
          width: "25ch",
          backgroundColor: "background.default",
          borderRadius: 1,
          margin: 0,
          marginRight: 2,
        }}
        value={data.interval}
        onChange={(newVal) => {
          const val = Number(newVal.target.value)
          if (!isNaN(val)) {
            setData((data) => {
              data.interval = val
              return { ...data }
            })
          }
        }}
      />
      <FormControl sx={{ m: 1, width: 300, margin: 0 }}>
        <InputLabel id="demo-multiple-name-label">Show types</InputLabel>
        <Select
          labelId="demo-multiple-name-label"
          id="demo-multiple-name"
          multiple
          value={data.showType}
          onChange={handleChange}
          input={<OutlinedInput label="Show types" />}
          renderValue={(selected) => selected.join(", ")}
          MenuProps={MenuProps}
          sx={{ backgroundColor: "background.default" }}
        >
          {names.map((name) => (
            <MenuItem key={name} value={name}>
              <Checkbox checked={data.showType.indexOf(name) > -1} />
              <ListItemText primary={name} />
            </MenuItem>
          ))}
        </Select>
      </FormControl>
      <div>
        <FormControlLabel
          control={
            <Checkbox
              checked={data.filterLength.filter}
              onChange={(event: React.ChangeEvent<HTMLInputElement>) => {
                setData((data) => {
                  data.filterLength.filter = event.target.checked
                  return { ...data }
                })
              }}
            />
          }
          label="Filter length"
          style={{ margin: 0, marginTop: 10 }}
        />
      </div>
      {data.filterLength.filter && (
        <div style={{ marginTop: 10 }}>
          <TextField
            label="Minimum length"
            id="outlined-start-adornment"
            sx={{
              m: 1,
              width: "25ch",
              backgroundColor: "background.default",
              borderRadius: 1,
              margin: 0,
              marginRight: 2,
            }}
            value={data.filterLength.minLength}
            onChange={(newVal) => {
              const val = Number(newVal.target.value)
              if (!isNaN(val)) {
                setData((data) => {
                  data.filterLength.minLength = val
                  return { ...data }
                })
              }
            }}
          />
          <TextField
            label="Maximum length"
            id="outlined-start-adornment"
            sx={{
              m: 1,
              width: "25ch",
              backgroundColor: "background.default",
              borderRadius: 1,
              margin: 0,
              marginRight: 2,
            }}
            value={data.filterLength.maxLength}
            onChange={(newVal) => {
              const val = Number(newVal.target.value)
              if (!isNaN(val)) {
                setData((data) => {
                  data.filterLength.maxLength = val
                  return { ...data }
                })
              }
            }}
          />
        </div>
      )}
      <div style={{ margin: 0, marginTop: 10 }}>
        <Button
          sx={{ marginRight: 2, backgroundColor: "background.default" }}
          onClick={() => handleBackwardClick()}
        >
          {"<"}
        </Button>
        <Button
          sx={{ marginRight: 2, backgroundColor: "background.default" }}
          onClick={() => handleForwardClick()}
        >
          {">"}
        </Button>
      </div>
      <div style={{ height: 100 }}></div>
    </div>
  )
}

export default SelectionHeader
